@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
html {
  font-family: 'Roboto', sans-serif;
  width: 100vw;
  color: black;
}
body {
  margin: 0;
  padding: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

*,
::after,
::before {
  box-sizing: border-box;
}

a {
  color: rgb(0, 0, 0);
}

.mainWrapper {
  padding: 0;
  padding-top: 100px;
  margin: 0 auto;
  max-width: 1440px;
}

.navList {
  margin: 0;
  width: 100%;
  max-width: 1440px;
  position: fixed;
  top: 0;
  display: -webkit-flex;
  display: flex;
  list-style: none;
  padding: 20px 0;
  border-bottom: 2px solid rgb(161, 161, 161);
  background-color: #fff;
}
.navLink {
  text-decoration: none;
  color: rgb(0, 61, 228);
  font-size: 24px;
}
.navListItem:not(:last-child) {
  margin-right: 20px;
}
.trendingHeader {
  padding-left: 40px;
}
.trendingList {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  list-style: none;
}
.trendingListItem {
  max-width: 300px;
  margin-right: 20px;
}
.btnComeBack {
  background-color: rgb(105, 105, 224);
  font-size: 20px;
  border: none;
  border-radius: 3px;
  color: #fff;
  padding: 10px;
  cursor: pointer;
}

